/* 기본 리셋 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  
  body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f8f9fa;
    color: #212529;
    line-height: 1.6;
  }
  
  /* Header */
  header {
    background-color: #343a40;
    color: white;
    padding: 20px 0;
    text-align: center;
  }
  
  header h1 {
    margin: 0;
    font-size: 2.5rem;
  }
  
  header p {
    font-size: 1.2rem;
    margin-top: 5px;
  }
  
  /* Section 공통 */
  section {
    padding: 60px 20px;
    max-width: 1000px;
    margin: 0 auto;
  }
  
  section h2 {
    font-size: 2rem;
    margin-bottom: 20px;
    border-bottom: 2px solid #ccc;
    padding-bottom: 10px;
  }
  
  /* Hero */
  .hero {
    background-color: #e9ecef;
    text-align: center;
    padding: 80px 20px;
  }
  
  .hero h2 {
    font-size: 2.5rem;
    margin-bottom: 10px;
  }
  
  .hero p {
    font-size: 1.2rem;
  }
  
  /* About */
  .about ul {
    list-style: none;
    padding-left: 0;
  }
  
  .about li {
    margin-bottom: 10px;
  }
  
  /* Projects */
  .project-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  
  .project-card {
    flex: 1 1 calc(50% - 20px);
    background-color: #fff;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 4px 6px rgba(0,0,0,0.05);
  }
  
  .project-card img {
    width: 100%;
    height: auto;
    display: block;
  }
  
  .project-card h3 {
    padding: 10px 15px 0;
    font-size: 1.3rem;
  }
  
  .project-card p {
    padding: 0 15px 10px;
    font-size: 1rem;
  }
  
  /* Contact */
  .contact p {
    font-size: 1.1rem;
  }
  
  /* Footer */
  footer {
    background-color: #343a40;
    color: white;
    text-align: center;
    padding: 15px 0;
    margin-top: 40px;
  }
  